                  <img :src="petForm.avatar" :alt="petForm.name">
                </div>
                <div class="upload-btn" @click="triggerFileInput">
                  <i class="el-icon-plus"></i>
                  <span>Êõ¥Êç¢Â§¥ÂÉè</span>
                </div>
                <input type="file" ref="fileInput" @change="handleAvatarUpload" accept="image/*"
                  style="display: none">
              </div>
