            </div>
            <div class="form-group">
              <label>‰ΩìÈáç(kg)</label>
              <input type="number" v-model="petForm.weight" class="form-input" placeholder="ËØ∑ËæìÂÖ•‰ΩìÈáç">
            </div>
            <div class="form-group">
              <label>ÁªùËÇ≤Áä∂ÊÄÅ</label>
              <div class="switch-group">
                <div class="switch-option" :class="{ active: petForm.sterilized }"
                  @click="petForm.sterilized = !petForm.sterilized">
