            <div class="form-group">
              <label>Áñ´ËãóÁä∂ÊÄÅ</label>
              <div class="switch-group">
                <div class="switch-option" :class="{ active: petForm.vaccinated }"
                  @click="petForm.vaccinated = !petForm.vaccinated">
                  <span class="icon">{{ petForm.vaccinated ? '‚úì' : '√ó' }}</span>
                  <span>{{ petForm.vaccinated ? 'Â∑≤Êé•Áßç' : 'Êú™Êé•Áßç' }}</span>
                </div>
              </div>
            </div>
          </div>
          <div class="dialog-footer">
            <button class="cancel-btn" @click="dialogVisible = false">ÂèñÊ∂à</button>
            <button class="confirm-btn" @click="submitForm">Á°ÆÂÆö</button>
          </div>
