</template>

<script>
export default {
  name: 'Pets',
  data() {
    return {
      pets: [
        {
          id: 1,
          name: 'Â∞èÁÅ∞ÁÅ∞',
          breed: 'Ëã±Áü≠',
          gender: 'male',
          age: 2,
          weight: 4.5,
          sterilized: true,
          vaccinated: true,
          avatar: require('@/assets/normal_cat.png')
        },
        {
          id: 2,
          name: 'Ëä±Ëä±',
          breed: 'ÁæéÁü≠',
          gender: 'female',
          age: 1,
          weight: 3.8,
          sterilized: true,
          vaccinated: true,
          avatar: require('@/assets/cute_cat.png')
        }
      ],
      dialogVisible: false,
      dialogType: 'add',
      petForm: {
        name: '',
        breed: '',
        gender: 'male',
        age: '',
        weight: '',
        sterilized: false,
        vaccinated: false,
        avatar: require('@/assets/normal_cat.png')
      }
    }
  },
  methods: {
    showAddDialog() {
      this.dialogType = 'add'
      this.petForm = {
        name: '',
        breed: '',
        gender: 'male',
        age: '',
        weight: '',
        sterilized: false,
        vaccinated: false,
        avatar: require('@/assets/normal_cat.png')
      }
      this.dialogVisible = true
    },
    editPet(pet) {
      this.dialogType = 'edit'
      this.petForm = { ...pet }
      this.dialogVisible = true
    },
    deletePet(pet) {
      if (confirm('Á°ÆËÆ§Âà†Èô§ËØ•ÂÆ†Áâ©Ê°£Ê°àÂêóÔºü')) {
        this.pets = this.pets.filter(p => p.id !== pet.id)
      }
    },
    submitForm() {
      if (this.dialogType === 'add') {
        this.pets.push({
          id: this.pets.length + 1,
          ...this.petForm
        })
      } else {
        const index = this.pets.findIndex(p => p.id === this.petForm.id)
        this.pets[index] = { ...this.petForm }
      }
      this.dialogVisible = false
    },
    triggerFileInput() {
      this.$refs.fileInput.click()
    },
    handleAvatarUpload(event) {
      const file = event.target.files[0]
      if (file) {
        const reader = new FileReader()
        reader.onload = (e) => {
          this.petForm.avatar = e.target.result
        }
        reader.readAsDataURL(file)
      }
      event.target.value = ''
    }
  }
}
</script>

